.App {
  text-align: center;
  height: 100vh;
  display: flex;

  /** We can use these to define our minimum height of the app container */
  min-height: 320px;
  min-width: 320px;

  align-items: center;
  justify-content: center;
  background-color: rgba(10, 10, 40);
}

div.row {
  display: flex;
  flex-direction: row;
}

div.col {
  display: flex;
  flex-direction: column;
}

.horizontal-divider-left {
  height: 2px;
  border-radius: 1px;
  background-image: linear-gradient(
    to right,
    rgba(255, 255, 255, 1),
    rgba(255, 255, 255, 0)
  );
  overflow: hidden;
  margin-top: 4px;
  margin-bottom: 4px;
  margin-right: 16px;
}

.horizontal-divider-right {
  height: 2px;
  border-radius: 1px;
  background-image: linear-gradient(
    to left,
    rgba(255, 255, 255, 1),
    rgba(255, 255, 255, 0)
  );
  overflow: hidden;
  margin-top: 4px;
  margin-bottom: 4px;
  margin-left: 16px;
}

.horizontal-divider-center {
  height: 2px;
  border-radius: 1px;
  overflow: hidden;
  background-image: linear-gradient(
    to right,
    rgba(255, 255, 255, 0),
    rgba(255, 255, 255, 1),
    rgba(255, 255, 255, 1),
    rgba(255, 255, 255, 1),
    rgba(255, 255, 255, 0)
  );
  margin-top: 4px;
  margin-bottom: 4px;
  margin-right: 16px;
  margin-left: 16px;
}
